html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background: -webkit-linear-gradient(left top, #F5FEFF, #C4EBEF);
  background: linear-gradient(to right bottom, #F5FEFF, #C4EBEF)
}

#root {
  min-height: 100%; 
}
.profile {
    background-color: rgb(255, 255, 255);
    padding: 15px;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    font-size: 20px;
}

.profile-greeting {
    font-size: 1.5em;
    text-align: center;
    margin-bottom: 35px;
}

.profile-logo {
    text-align: center;
    margin-bottom: 50px;
}

.profile-logo-background {
    background-color: rgba(196, 235, 239, 0.3);
    text-align: center;
    height: 175px;
    margin-bottom: 20px;
}

.profile-logo-frame {
    background-color: rgba(175, 175, 175, 1);
    border-radius: 50%;
    width: 175px;
    height: 175px;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.profile-add-photo {
    font-size: 0.75em;
}

.profile-data-input {
    margin-bottom: 20px;
}

.profile-about-itself-input {}

.profile-interests-select {
    margin-top: 50px;
    padding-bottom: 45px;
    text-align: center;
    border-bottom: 1px solid rgba(234, 234, 234, 1);
}

.profile-interest-wrapper {
    display: inline-block;
    margin: 5px;
    cursor: pointer;
}

.profile-interest-checkbox {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100px;
    height: 100px;
    font-size: 14px;
    background-color: rgba(234, 234, 234, 1);
    color: rgba(175, 175, 175, 0.5);
    border: 10px solid rgba(234, 234, 234, 1);    
}

.profile-interest-checkbox input {
    display: none;
}

.profile-interest-checkbox span {
    text-transform: uppercase;
    font-size: 1em;
    font-weight: bold;
}

.profile-interest-selected {
    background: url(/static/media/check-solid.fcfb77cf.svg) rgba(222, 170, 93, 1);
    color: rgb(255, 255, 255);
    border: 10px solid rgba(244, 197, 127, 1);    
}

.profile-about-itself {
    width: 100%;
    height: 100px;
}
.text-input-wrapper {
    display: inline-block;
    position: relative;
    padding: 0;
    padding-top: 1em;
}

.text-input {
    display: block;
    border: none;
    outline: none;
    padding: 10 0;
    font-size: 1em;
    border-bottom: 1px solid rgba(234, 234, 234, 1);
}

.text-input:focus {
    border-color: rgba(222, 170, 93, 1); 
}

.label-info {
    position: absolute;
    right: 0;
    top: 1.25em;
    -webkit-transition: 0.3s linear;
    transition: 0.3s linear;
}

.text-input:focus ~ .label-info,
.filled {
    top: 0;
    font-size: 0.75em;
    color: rgba(222, 170, 93, 1);
}
.btn-wrapper {
    cursor: pointer;
    padding: 0 2px 2px 0;
}

.btn-border {
    position: relative;
    background: transparent;
    border: 1px solid rgba(175, 175, 175, 1);
}

.btn {
    cursor: pointer;
    display: block;
    text-transform: uppercase;
    background-color: rgba(67, 164, 174, 1);
    padding: 0.75em 0;
    width: 100%;
    color: white;
    border: none;
    outline: none;
    text-align: center;
    -webkit-transform: translate(2px, 2px);
            transform: translate(2px, 2px);
}

.btn-wrapper:active .btn,
.btn:active {
    background-color: rgba(175, 223, 228, 1);
}

.btn-disabled {
    background-color: rgba(234, 234, 234, 1) !important;
}
.toggler {
    width: 50px;
    height: 20px;
    padding-top: 15px;
}

.toggler-icon::before,
.toggler-icon::after,
.toggler-icon {
    display: block;
    height: 5px;
    width: 50px;
    background: #367A7A;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
}

.toggler-icon::before,
.toggler-icon::after {
    content: "";
}

.toggler-icon::before {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
}

.toggler-icon::after {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
}

.toggler-icon-expanded {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.toggler-icon-expanded:before,
.toggler-icon-expanded:after {
    width: 50%;
}

.toggler-icon-expanded:before {
    -webkit-transform: translate(26px, -10px) rotate(45deg);
            transform: translate(26px, -10px) rotate(45deg);
}

.toggler-icon-expanded:after {
    -webkit-transform: translate(26px, 5px) rotate(-45deg);
            transform: translate(26px, 5px) rotate(-45deg);
}
.nav-icon {
    margin-right: 10px;
}

.nav-link,
.nav-link:active,
.nav-link:hover {
    display: block;
    text-decoration: none !important;
    font-size: 19px;
    color: rgb(0, 0, 0);
    position: relative;
    padding: 22px 25px 22px 0;
    border-bottom: 1px solid rgb(234, 234, 234);
}

.proceed {
    position: absolute;
    right: 0px;
}

.nav-link-active {
    border-color: rgba(222, 170, 93, 1);
    color: rgba(222, 170, 93, 1);
}

.nav-link-active .nav-icon {
    stroke: rgba(222, 170, 93, 1) !important;
}
.checkmark:before,
.checkmark:after {
    display: inline-block;
    content: "";
    height: 2px;
    width: 10px;
    background-color: rgb(175, 175, 175);
}

.checkmark:before {
    -webkit-transform: translate(10px, -5px) rotate(60deg);
            transform: translate(10px, -5px) rotate(60deg);
}

.checkmark:after {
    -webkit-transform: translateY(3px) rotate(-60deg);
            transform: translateY(3px) rotate(-60deg);
}
.logo {
    position: relative;
    width: 180px;
    height: 24px;
    text-align: center;
}

.logo-dash {
    position: absolute;
    bottom: 13px;
    display: block;
    width: 21px;
    height: 1px;
    background-color: rgba(54, 122, 122, 1);
}

.logo-dash-left {
    left: 0;
}

.logo-dash-right {
    right: 0;
}

.logo-text {
    text-transform: uppercase;
    font-size: 18px;
    color: rgb(255, 255, 255);
    text-shadow: -1px -1px 0 rgba(54, 122, 122, 1),
        1px -1px 0 rgba(54, 122, 122, 1),
        -1px 1px 0 rgba(54, 122, 122, 1),
        1px 1px 0 rgba(54, 122, 122, 1);
}
.modal {
    float: left;
    z-index: 10000;
}

.modal-expanded {
    position: fixed;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.1);
}

.nav-container {
    position: relative;
    cursor: pointer;
    padding-top: 33px;
    padding-left: 23px;
    padding-right: 13px;
}

.nav-container::after {
    display: block;
    content: ""
}

.nav-container-expanded {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
    width: 300px;
    height: 100%;
    box-sizing: border-box;
    background: rgb(255, 255, 255);
}

.nav-header {}

.nav-toggler {
    float: right;
}

.nav-hidding-area {
    display: none;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

.modal-expanded .nav-hidding-area {
    display: block;
    padding-top: 35px;
    margin-right: 15px;
}


.navbar {
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
    width: 0;
    overflow-x: hidden;
}

.navbar-expanded {
    padding-top: 35px;
    width: auto;
}

.nav-footer {
    position: absolute;
    bottom: 26px;
    color: rgb(175, 175, 175);
    font-size: 14px;
    text-align: center;
}

.navbar-expanded .nav-logo {
    position: absolute;
    top: 37px;
    left: 20px;
}

.nav-footer div {
    margin-top: 14px;
}

.nav-footer-icon {
    margin-left: 5px;
}

.main-header {
    height: 75px;
}

.main-header::after {
    display: block;
    content: "";
}

.main-content {
    margin-top: 25px;
    padding: 15px;
    clear: both;
    display: -webkit-flex;
    display: flex;
}

